div {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
.recipes {
    padding: 5px;
    font-family: sans-serif;
}

.video-container {
	position:relative;
	padding-bottom:56.25%;
	padding-top:30px;
	height:0;
	overflow:hidden;
}

.video-container iframe, .video-container object, .video-container embed {
	position:absolute;
	top:0;
	left:0;
	width:100%;
	height:100%;
}

#map {
  height: 91vh;
  width: 100vw;
  position: fixed;
}
